import React from 'react';
import {Card,Button} from 'react-bootstrap';
import SearchLead from '../component/SearchLead';
import {searchLead} from '../../../actions/lead_actions';
import { CSVLink, CSVDownload } from "react-csv";
import ReactTable from "react-table"; 
import {format_date_tbl,capitalize_first_letter} from '../../../helpers/common_helper';
import {dd_value} from '../../../actions/common_actions';
import { Link} from 'react-router-dom';
import _ from 'lodash';
import FlowContainer from '../../common/container/core/FlowContainer';

class LeadSearchContainer extends FlowContainer {
    state ={
        lead :{},
        columns :[],
        lead_list:[],
        lead_table:"hide_div"
    }

	validate_search(lead) {
		if((lead.account_num && !lead.acc_prvdr_code) || (!lead.account_num && lead.acc_prvdr_code) ){
            alert("Please select account provider")
            return false  
        }

		if(lead.created_from || lead.created_to || lead.flow_rel_mgr_id || lead.territory
			|| lead.acc_prvdr_code || lead.acc_prvdr_code || lead.account_num || lead.location ||
			lead.mobile_num || lead.status ){
		 	return true
  		}else{
  			alert("Please enter valid search criteria")
  			return false
  		}

		
	}


    handleSearch =(event)=>{
		console.log(event)
		var is_valid = this.validate_search(this.state.lead)
		if(is_valid){
			searchLead(this.req(this.state.lead))
			.then((response) => {
				this.resp(response)
				if(!response){return };
				if(response.status === "success"){
					this.resp(response)
					var columns = this.state.columns
						columns = [{  
										Header: 'Cust name (or) Biz name',  
										id: 'biz_name' ,
										accessor :row =>dd_value(row.biz_name)
									},{  
										Header: 'Mobile Number',  
										accessor: 'mobile_num' ,
									},{  
										Header: 'Account Number',  
										accessor: 'account_num' ,
										width: 250,
									},{  
										Header: 'Location',  
										id: 'location' ,
										accessor :row =>capitalize_first_letter(row.location)
									},{  
										Header: 'Territory',  
										id: 'territory' ,
										accessor :row =>capitalize_first_letter(row.territory)
									},{  
										Header: 'Created From',
										id: 'created_at', 
										accessor:row => format_date_tbl(row.created_at,true)  
									},{  
										Header: 'View / Edit',  
										accessor: 'id' ,
										Cell: e =>  <Link className="btn btn-primary" role="button" to={"/lead/edit/"+e.value} target="_blank" >View / Edit</Link>,
									}]
								}
								this.setState({columns,lead_list:response.data,lead_table:"show_div"})

			})
		}
       
    }

    render(){
        return(
            <div className="container " style={{maxWidth :"1500px"}}>
                <br/>
                <br/>
                <form className="mb-3" autocomplete="off">
                    <Card>
                        <Card.Header style = {{backgroundColor: "#01062c" , color: "white"}}><h4>{this.props.title}</h4></Card.Header>
                            <Card.Body className = "p-5">
                            	<SearchLead id = "lead" onComponentChange={this.handleComponentChange}  />
                                <div className="form-group row reset-margin justify-content-center py-4">
                                    <Button  disabled={this.state.calling_api} className= {`col-md-2 mr-3 btn btn-primary${this.state.submit_btn_style}`} onClick={this.handleSearch} >Search</Button>
                                </div>
                            </Card.Body>
                        </Card>
                </form>
                {!_.isEmpty(this.state.lead) &&
                <div className={`mt-3 ${this.state.lead_table}`} >
					<CSVLink  filename={"lead.csv"} data={this.state.lead_list} className="btn btn-secondary mb-3">Export as CSV</CSVLink>
    				<ReactTable  minRows = {2}  data={this.state.lead_list}  columns={this.state.columns}  />
    			</div>
            }
            </div>        )
    }
}

export default LeadSearchContainer;