import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import {Alert, Row, Col } from 'react-bootstrap';
import '../../../styles/flow.css';
import {capitalize_first_letter} from '../../../helpers/common_helper';
import {dd_value} from '../../../actions/common_actions';

class AddressPanel extends FlowComponent {

	render(){
		return(					
			<>
			<Alert variant="" className="floatAdvancetitleBox agreementDateHeaderBox">
				<Alert.Heading className="floatAdvanceperiodHeaderBox">
					<h2	 className={"floatAdvanceperiodHeader"}>{this.props.title}</h2>
				</Alert.Heading>
				<Row className="pl-5 persontxt">
						{
						Object.entries(this.props.data).map(([key, value])=> {

				    	return(
				    	key != "id"	&&
							<div key={key} className="col-md-6 row pl-5">
								<Col>{dd_value(key,'address_fields')}</Col>
								<Col><b>{capitalize_first_letter(value)}</b></Col>
							</div>
						
				    	);})
    					}
					{this.props.data.photo_shop_path &&
					<Col lg="3">
							<b>Establishment Photo</b>
							<img src={this.root + this.props.data.photo_shop_path + "/s_" + this.props.data.photo_shop} 
							alt={this.props.data.photo_shop_path + "/s_" + this.props.data.photo_shop}
							/>
					</Col>
					}
				</Row>	
			</Alert>
			</>
			);
	}
}
export default AddressPanel;
