import {  Redirect } from 'react-router-dom';
import React from 'react';
import {Card,Button} from 'react-bootstrap';
import Lead from '../component/Lead';
import FlowContainer from '../../common/container/core/FlowContainer';
import {submitLead,viewLead} from '../../../actions/lead_actions';
import _ from 'lodash';
import { Link } from 'react-router-dom';
import { check_priv } from '../../../helpers/storage_helper';


 class LeadContainer extends FlowContainer {
    
    state = {
        comp_name: "lead",
        lead_list : {},
        to_edit: false, 
        lead : {},
        lead_data : {}
    }

    componentWillMount(){
        if(this.props.mode == "view"){
            this.id = this.props.match.params.id;
             viewLead(this.req({id: this.id}))
            .then((response) => {
                if(!response){return };
                if(response.status === "success"){
                    this.setState({lead : response.data,div_style : "disable_div",submit_btn_style : "hide_div"})
                }
            })
        }else if(this.props.mode == "create"){
        }
    }

    validate_fields(lead_data) {
        if(lead_data.product_group == "terminal_financing" && !lead_data.mobile_num){
            alert("Please enter mobile number")
            return false  
        }

        if(lead_data.account_num && !lead_data.acc_prvdr_code){
            alert("Please select account provider")
            return false  
        }

		if( lead_data.landmark && lead_data.acc_prvdr_code  && lead_data.product_group  &&
            lead_data.biz_name ){
			return true
  		}else{
  			alert("Please enter required fields")
  			return false
  		}
	}

    handleLead = (event) =>{
      const lead_data = this.state.lead 
      var is_valid = this.validate_fields(lead_data)
      if(is_valid){
        submitLead(this.req({"lead":lead_data})).
               then((response) =>{
                    this.resp(response)
                    if(!response){return };
                    if(response.status === "success"){
                        alert(response.message); 
                        this.setState({to_edit : true,id:response.data})
                    }
                })
      }
    }

    render(){

        if(this.state.to_edit === true) {
            return <Redirect to={`/lead/edit/${this.state.id}`}/> 
        }

        return(
            <div className="container " style={{maxWidth :"1500px"}}>
                <br/>
                <br/>
                <form autocomplete="off">
                    <Card>
                        <Card.Header style = {{backgroundColor: "#01062c" , color: "white"}}>
                            <h4>{this.props.title}
                            {this.state.lead.status === "50_pending_audit" && check_priv("lead","audit_kyc") &&
                                <Button className= 'float-right' onClick={this.handleAudit}><Link to={`/lead/audit_kyc/${this.id}`} className="text-white">Audit KYC</Link></Button>
                            }
                            </h4></Card.Header>
                            <Card.Body className = "p-5">
                                {(this.props.mode == 'create' || !_.isEmpty(this.state.lead)) &&
                                    <Lead mode={this.props.mode} editable="true" id = "lead" data = {this.state.lead} onComponentChange={this.handleComponentChange} />
                                }
                                <div className="form-group row reset-margin justify-content-center py-4">
                                    <Button  disabled={this.state.calling_api} className= {`col-md-2 mr-3 btn btn-primary${this.state.submit_btn_style}`} onClick={this.handleLead} >Submit</Button>
                                </div>
                            </Card.Body>
                        </Card>
                </form>
            </div>
        )
    }
}

export default LeadContainer;
