class AdminHome extends FlowComponent {
    state = {
        toSelectMarket:false
    }
  constructor(props){
    super(props)
    this.load_home_accounts = this.load_home_accounts.bind(this)
  }
 
  async componentWillMount() {
   // window.market = "IN";
   //const logout = touch();
    const logout = await super.touch_async()
    this.setState({logout})


    this.load_home_accounts()

    setInterval(() => this.setState({refresh_count: this.state.refresh_count + 1}),60000)
    
  }
 
  load_home_accounts=()=>{
   
    if(window.location.pathname !== '/'){
        return
    }
    var account = {lender_code : 'UFLW', "country_code": this.country_code, 
                    lender_acc_prvdr_code : this.acc_prvdr_code, to_recon : true};
    
    
    getRefAccounts(this.req({account})).then((response) => {
        if(!response){return };
        if(response.status === "success"){
            if(response.data != "")
            {
                const account_list = response.data.map((account,i)=>
                
                <li><Link to={`/account/${account.id}/statement` } target = "blank">{account.name}</Link></li>
                
                );
                this.setState({account_list})
                
            }     
            
        }else{
        
            alert(response.message + "\n" + JSON.stringify(response.data));  
        }
        
    });



}
state = {
      refresh_count: 0
    //logout : super.touch_async()

  }

/*   touch(){
     var logout = false;
       
      touch({country_code : 'UGA'})
               .then((response) => {
                     if(!response){return };
                     
                     if(response.status === "success1"){
                     }else{
                        userService.logout();
                        logout = true;
                        alert("Session Expired")
                        //window.location.reload('/login')
                     }
                  }
                ); 
                
    return logout;
  }*/
    selectAccPrvdr = (event) => {
        if(this.roles === "super_admin"){
            this.toChange = "acc_prvdr";
        }else{
            this.toChange = "acc_prvdr_only";
        }
        this.setState({toSelectMarket:true});

    }

    selectMarket = (event) => {

        if(get('role_codes') ==="super_admin"){
            this.toChange = "market";
            this.setState({toSelectMarket:true});
        }
    }

  
  render() {
     //var ReactSignature = require('react-signature');
     console.log(window.location.pathname);
      if(this.state.toSelectMarket){
          return (<SelectMarket toChange={this.toChange} action="change" onClose={()=>this.setState({toSelectMarket: false})} />);
      }
      if(this.state.logout){
          //return <Redirect to='/login'  />
        return <></>
      
      }
    const role_codes = get('role_codes');
    const admin_roles = ['super_admin', 'ops_admin', 'market_admin']
    return (
          <div className="wrapper">
            <AdminMenu load_home_accounts = {this.load_home_accounts}/>
            <div className="main-panel">
            <nav className="navbar navbar-expand-lg navbar-transparent navbar-absolute fixed-top pb-3">
              <div className="container-fluid">
                <div className="navbar-wrapper">
                  <div className="navbar-minimize">
                    <button id="minimizeSidebar" className="btn btn-just-icon btn-white btn-fab btn-round">
                      <i className="material-icons text_align-center visible-on-sidebar-regular">more_vert</i>
                      <i className="material-icons design_bullet-list-67 visible-on-sidebar-mini">view_list</i>
                    </button>
                  </div>
                </div>
                <button className="navbar-toggler" type="button" data-toggle="collapse" aria-controls="navigation-index" aria-expanded="false" aria-label="Toggle navigation">
                  <span className="sr-only">Toggle navigation</span>
                  <span className="navbar-toggler-icon icon-bar"></span>
                  <span className="navbar-toggler-icon icon-bar"></span>
                  <span className="navbar-toggler-icon icon-bar"></span>
                </button>
                <div className="collapse navbar-collapse justify-content-end">
                  <ul className="navbar-nav">
                      <li>
                          <div className={get('role_codes') == 'super_admin' && "flag_cursor"} onClick={this.selectMarket}>
                              <Flag id="market" name={this.country_code} format="png" pngSize={48} shiny={false} alt={this.country_code} basePath="/img/flags"/>
                          </div>
                      </li>
                      {admin_roles.includes(role_codes) &&
                        <li>
                            <div className="flag_cursor pl-5" onClick={this.selectAccPrvdr}>
                                <Logo file_name={"t_"+get('acc_prvdr_logo')} entity="acc_prvdr"
                                      entity_code={get('acc_prvdr_code')}
                                      country_code={this.country_code}
                                      color="logo_style" />&nbsp;&nbsp;
                            </div>
                        </li>
                      }
                    <li className="nav-item dropdown">
                      <Link to={"/logout"} className="nav-link logoutLink" href="javascript:;" id="navbarDropdownProfile" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        <i className="material-icons fs-2">logout</i>
                        <p className="d-lg-none d-md-block">
                          Account
                        </p>
                      </Link>
                    </li>
                  </ul>
                </div>
              </div>
            </nav>
              <div className="content">
                <div className="container-fluid">
                  {CustomerRoutes}
                  {FLOWRelMgrRoutes}
                  {DPRelMgrRoutes}
                  {LoanApplRoutes}
                  {LoanRoutes}
                  {MarketRoutes}
                  {LenderRoutes}
                  {DataPrvdrRoutes}
                  {AccPrvdrRoutes}
                  {ProductRoutes}
                  {MasterDataRoutes}
                  {AgreementRoutes}
                  {ScoreRoutes}
                  {Reports}
                  {AccountRoutes}
                  {FieldvisitRoutes}
                  {KYCRoutes}
                  {CallLogRoutes}
                  {PersonRoutes}
                  {LeadRoutes}


                  {window.location.pathname == "/" &&
                  <div className = "row home-menu-container mb-5">
                    <HomeLinks />
                    {this.state.account_list && check_priv('recon', 'search') &&
                      <div className ="home-menu box col-lg-3 col-md-6 text-center">
                          <div className="card card-pricing ">
                            <div className="card-body">
                              <h3 className="card-title">Wallet Statements</h3>
                              <p className="card-description">
                                <ul>
                                    {this.state.account_list}
                                </ul>
                              </p>
                            </div>
                          </div>
                      </div>
                    }
                  </div>
                  }

                    {window.location.pathname == "/" && check_priv('application', 'approval') &&

                    <div className="">
                        <LoanPendingApproval/>
                    </div>

                    }


                    {window.location.pathname == "/" && check_priv('cust_agreement', 'expired_list') &&
                    <div className="home_pend_approvals">
                        <ExpiredAgreement/>
                    </div>
                    }

                  {window.location.pathname == "/" && check_priv('application','delayed_approval_list') &&
                    <div className="home_stalled_fas">
                      <DisplayList list_name='pending_w_rm' refresh={this.state.refresh_count}/>
                    </div>
                  }
                  {window.location.pathname == "/" && check_priv('loan','pending_confirmation_list') &&
                    <div className="home_stalled_fas">
                      <DisplayList list_name='pending_w_cust' refresh={this.state.refresh_count}/>
                    </div>
                  }

                    {window.location.pathname == "/"  && check_priv('loan', 'hold_loan_list') &&
                    <div className="home_stalled_fas">
                      <OnHoldList refresh={this.state.refresh_count}/>
                    </div>
                    }

                    {window.location.pathname == "/" && check_priv('customer','perform_kyc') &&
                    <div className="home_stalled_fas">
                        <KycPendingList/>
                    </div>
                  }


                    {window.location.pathname == "/"  && check_priv('loan', 'recovery_list') &&
                    <div className="home_stalled_fas">
                        <OngoingRecoveryList/>

                    </div>
                    }

                  
                </div>   
              </div>     
            </div>   
          </div>
    );
  }
}

export default AdminHome;
