import {call_fetch, admin_path} from './../helpers/fetch_helper';

export const reviewRules = (request) =>{
    
    return call_fetch(admin_path + '/kyc/', request);
}

export const updateKYCRules = (request) =>{
    
   return call_fetch(admin_path + '/kyc/update_rules', request);
}

export const approveKYC = request => {

    return call_fetch(admin_path + '/audit_kyc/cust_reg', request);
}

export const updateLeadStatus = request => {

    return call_fetch(admin_path + '/audit_kyc/update', request);
}
 